
.App {
  text-align: center;
}

body{
  background:url("https://source.unsplash.com/random/?black/");
  background-size: cover;
  background-attachment: fixed;
  
}
 #main{
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  
}
#main h1{
  color:white;
  padding:2rem 2rem;
}
.info{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.info img{
  width: 20%;
  border-radius:50%;

}
.in2{
  display: flex;
  flex-direction: row;
}
.card{
  padding:1rem 1rem;
  width: 50%;
  background-color: #14075013;
  color:white;
  border-radius: 10%;
}
.card a{
  color:rgb(189, 209, 118);
}
.tt{
  color:#c4a578
}
