@media only screen and (max-width: 600px){
  .info{
    font-size: 1.2rem;
    display: flex;
    width:100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .info img{
    width: 50%;
    border-radius:50%;
  
  }
  .in2{
    display: flex;
    flex-direction: row;
  }
  .card{
    /* border: 10px solid red; */
    width: 100%;
    padding:1rem 1rem;
    background-color: #14075013;
    color:white;
    border-radius: 10%;
  }
  .card a{
    color:rgb(189, 209, 118);
  }
  .tt{
    color:#eed2a5;
    font-size: 1.5rem;
  }

}